import React from "react";
import styled from "styled-components";
import Fade from "react-reveal/Fade";
function Section({
  title,
  description,
  backgroundImg,
  leftTxtBtn,
  rightTxtBtn,
}) {
  return (
    <Wrap bgImage={backgroundImg}>
      <Fade bottom cascade>
        <ItemText>
          <h1>{title}</h1>
          <p>{description}</p>
        </ItemText>
      </Fade>

      <Buttons>
        <ButtonGroup>
          <Fade left big>
            <LeftButton>{leftTxtBtn}</LeftButton>
          </Fade>
          {rightTxtBtn && (
            <Fade right big>
              <RightButton>{rightTxtBtn}</RightButton>
            </Fade>
          )}
        </ButtonGroup>
        <DownArrow src="/images/down-arrow.svg" />
      </Buttons>
    </Wrap>
  );
}

export default Section;

const Wrap = styled.div`
  width: 100vw;
  height: 100vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-image: ${(props) => `url("/images/${props.bgImage}")`};
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
`;

const ItemText = styled.div`
  padding-top: 15vh;
  margin-top: 15px;
  text-align: center;
  h1 {
    letter-spacing: 1.5px;
    font-size: 2.2rem;
  }
  p {
    margin-top: 5px;
    font-size: 0.8rem;
    color: #495057;
  }
`;

const ButtonGroup = styled.div`
  display: flex;
  margin-bottom: 15px;
  @media (max-width: 768px) {
    flex-direction: column;
  } ;
`;
const LeftButton = styled.div`
  font-size: 0.7rem;
  font-weight: 500;
  background-color: rgba(23, 26, 32, 0.8);
  height: 37px;
  width: 235px;
  color: White;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100px;
  opacity: 0.85;
  cursor: pointer;
  margin: 8px 10px;
  text-transform: uppercase;
`;
const RightButton = styled(LeftButton)`
  margin-top: 10px;
  background: white;
  opacity: 0.65;
  color: black;
`;

const DownArrow = styled.img`
  color: black;
  height: 40px;
  overflow-x: hidden;
  animation: animateDown infinite 1.5s;
  margin-bottom: 20px;
`;
const Buttons = styled.div``;
